import { Component, Prop, h, Element, getAssetPath } from '@stencil/core';
import { i18n } from '../../utils/i18n';
import { prettyDate, round } from '../../utils/utils';

@Component({
  tag: 's3p-expenses-report',
  styleUrl: 's3p-expenses-report.css',
  shadow: false,
})
export class S3pExpensesReport {
  @Element() element: HTMLElement;
  i18n: any;

  @Prop() driverName: string;
  @Prop() driverCard: string;
  @Prop() start;
  @Prop() end;
  @Prop() expenses;

  async componentWillLoad() {
    this.i18n = new i18n(this.element);
    await this.i18n.load(getAssetPath('../'));
  }

  render() {
    return (
      <div class="report">
        <header>
          <img class="logo" src={'https://app.mapandtruck.io/assets/mapandtruck/logo_maptrcuk.png'} height="40" alt="s3pweb"></img>
          <div class="info-driver">
            <p>Conducteur: {this.driverName.toUpperCase()} </p>
            <p>N° Carte: {this.driverCard} </p>
          </div>
          <div class="info-driver">
            <p>Détails des frais</p>
            <p>
              Période du {prettyDate(this.start, true)} au {prettyDate(this.end, true)}
            </p>
          </div>
        </header>
        <main>
          {this.expenses &&
            this.expenses.month &&
            Object.keys(this.expenses.month).map((month, index) => (
              <table>
                <thead>
                  <tr>
                    <th></th>
                    <th colSpan={2}>Unique</th>
                    <th colSpan={2}>Matin</th>
                    <th colSpan={2}>Midi</th>
                    <th colSpan={2}>Soir</th>
                    <th colSpan={2}>Nuit</th>
                    <th>Total</th>
                    <th colSpan={2}>Prime</th>
                  </tr>
                </thead>
                <tbody>
                  {Object.keys(this.expenses.month[month].week).map(week => [
                    Object.keys(this.expenses.month[month].week[week].day).map(day => (
                      <tr>
                        <td>{new Date(day).toLocaleString('fr-Fr', { weekday: 'short' }) + ' ' + prettyDate(new Date(day), true)}</td>
                        <td>{this.expenses.month[month].week[week].day[day].unique ? this.expenses.month[month].week[week].day[day].unique.count : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].unique ? round(this.expenses.month[month].week[week].day[day].unique.amount, 2) : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].morning ? this.expenses.month[month].week[week].day[day].morning.count : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].morning ? round(this.expenses.month[month].week[week].day[day].morning.amount, 2) : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].lunchTime ? this.expenses.month[month].week[week].day[day].lunchTime.count : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].lunchTime ? round(this.expenses.month[month].week[week].day[day].lunchTime.amount, 2) : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].evening ? this.expenses.month[month].week[week].day[day].evening.count : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].evening ? round(this.expenses.month[month].week[week].day[day].evening.amount, 2) : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].night ? this.expenses.month[month].week[week].day[day].night.count : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].night ? round(this.expenses.month[month].week[week].day[day].night.amount, 2) : ''}</td>
                        <td>{round(this.expenses.month[month].week[week].day[day].totals, 2)}</td>
                        <td>{this.expenses.month[month].week[week].day[day].bonus ? this.expenses.month[month].week[week].day[day].bonus.count : ''}</td>
                        <td>{this.expenses.month[month].week[week].day[day].bonus ? round(this.expenses.month[month].week[week].day[day].bonus.amount, 2) : ''}</td>
                      </tr>
                    )),
                    <tr class={'week'}>
                      <td>Semaine {week}</td>
                      <td>{this.expenses.month[month].week[week].unique ? this.expenses.month[month].week[week].unique.count : ''}</td>
                      <td>{this.expenses.month[month].week[week].unique ? round(this.expenses.month[month].week[week].unique.amount, 2) : ''}</td>
                      <td>{this.expenses.month[month].week[week].morning ? this.expenses.month[month].week[week].morning.count : ''}</td>
                      <td>{this.expenses.month[month].week[week].morning ? round(this.expenses.month[month].week[week].morning.amount, 2) : ''}</td>
                      <td>{this.expenses.month[month].week[week].lunchTime ? this.expenses.month[month].week[week].lunchTime.count : ''}</td>
                      <td>{this.expenses.month[month].week[week].lunchTime ? round(this.expenses.month[month].week[week].lunchTime.amount, 2) : ''}</td>
                      <td>{this.expenses.month[month].week[week].evening ? this.expenses.month[month].week[week].evening.count : ''}</td>
                      <td>{this.expenses.month[month].week[week].evening ? round(this.expenses.month[month].week[week].evening.amount, 2) : ''}</td>
                      <td>{this.expenses.month[month].week[week].night ? this.expenses.month[month].week[week].night.count : ''}</td>
                      <td>{this.expenses.month[month].week[week].night ? round(this.expenses.month[month].week[week].night.amount, 2) : ''}</td>
                      <td>{round(this.expenses.month[month].week[week].totals, 2)}</td>
                      <td>{this.expenses.month[month].week[week].bonus ? this.expenses.month[month].week[week].bonus.count : ''}</td>
                      <td>{this.expenses.month[month].week[week].bonus ? round(this.expenses.month[month].week[week].bonus.amount, 2) : ''}</td>
                    </tr>,
                  ])}
                  <tr class={'month'}>
                    <td>{new Date(month).toLocaleString('fr-Fr', { month: 'long', year: 'numeric' })}</td>
                    <td>{this.expenses.month[month].unique ? this.expenses.month[month].unique.count : ''}</td>
                    <td>{this.expenses.month[month].unique ? round(this.expenses.month[month].unique.amount, 2) : ''}</td>
                    <td>{this.expenses.month[month].morning ? this.expenses.month[month].morning.count : ''}</td>
                    <td>{this.expenses.month[month].morning ? round(this.expenses.month[month].morning.amount, 2) : ''}</td>
                    <td>{this.expenses.month[month].lunchTime ? this.expenses.month[month].lunchTime.count : ''}</td>
                    <td>{this.expenses.month[month].lunchTime ? round(this.expenses.month[month].lunchTime.amount, 2) : ''}</td>
                    <td>{this.expenses.month[month].evening ? this.expenses.month[month].evening.count : ''}</td>
                    <td>{this.expenses.month[month].evening ? round(this.expenses.month[month].evening.amount, 2) : ''}</td>
                    <td>{this.expenses.month[month].night ? this.expenses.month[month].night.count : ''}</td>
                    <td>{this.expenses.month[month].night ? round(this.expenses.month[month].night.amount, 2) : ''}</td>
                    <td>{round(this.expenses.month[month].totals, 2)}</td>
                    <td>{this.expenses.month[month].bonus ? this.expenses.month[month].bonus.count : ''}</td>
                    <td>{this.expenses.month[month].bonus ? round(this.expenses.month[month].bonus.amount, 2) : ''}</td>
                  </tr>
                  {index === Object.keys(this.expenses.month).length - 1 ? (
                    <tr id="totalPeriod">
                      <td>Période</td>
                      <td>{this.expenses.unique ? this.expenses.unique.count : ''}</td>
                      <td>{this.expenses.unique ? round(this.expenses.unique.amount, 2) : ''}</td>
                      <td>{this.expenses.morning ? this.expenses.morning.count : ''}</td>
                      <td>{this.expenses.morning ? round(this.expenses.morning.amount, 2) : ''}</td>
                      <td>{this.expenses.lunchTime ? this.expenses.lunchTime.count : ''}</td>
                      <td>{this.expenses.lunchTime ? round(this.expenses.lunchTime.amount, 2) : ''}</td>
                      <td>{this.expenses.evening ? this.expenses.evening.count : ''}</td>
                      <td>{this.expenses.evening ? round(this.expenses.evening.amount, 2) : ''}</td>
                      <td>{this.expenses.night ? this.expenses.night.count : ''}</td>
                      <td>{this.expenses.night ? round(this.expenses.night.amount, 2) : ''}</td>
                      <td>{round(this.expenses.totals, 2)}</td>
                      <td>{this.expenses.bonus ? this.expenses.bonus.count : ''}</td>
                      <td>{this.expenses.bonus ? round(this.expenses.bonus.amount, 2) : ''}</td>
                    </tr>
                  ) : (
                    ''
                  )}
                </tbody>
              </table>
            ))}
        </main>
      </div>
    );
  }
}
