/* impression */

html {
  -webkit-print-color-adjust: exact;
}

/* page */

body {
  margin: 0;
  background-color: white;
}
main {
  margin: 10px 10px 10px 10px;
  font-size: 11px;
}
.report {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* header */

header {
  display: flex;
  align-items: center;
  padding: 10px 10px 10px 10px;
  font-weight: bold;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  margin-bottom: 10px;
}
header p {
  margin: 5px 0;
}

/* table */

table {
  width: 100%;
  font-size: 11px;
  margin-top: 20px;
}
table,
th,
td {
  border: 0.1px solid white;
}
th,
td {
  padding: 1px;
  text-align: center;
}
th {
  text-transform: uppercase;
  background: rgba(var(--primary-color-rgba, 249, 190, 31), 0.6);
  width: 100px;
}
tr {
  background: rgb(255, 240, 172, 0.2);
}
td {
  text-transform: capitalize;
}

/* color */

.week {
  background: rgb(255, 240, 172);
}
.month {
  background: rgba(var(--primary-color-rgba, 249, 190, 31), 0.6);
}
#totalPeriod {
  background: #f1f1f1;
}

/* other */

.text-center {
  text-align: center;
}
.info-driver {
  margin: auto;
}
